const initialState = {
    dialogMode: 0,//0 - , 1 - 
};

export default (state = initialState, action) => {
    switch (action.type) {
        case 'SET_DIALOG_MODE':
            return {...state, dialogMode: action.payload};
        default:
            return state
    }
};

//e   dialogMode(  )
export function setDialogMode(mode) {
    return {
        type: 'SET_DIALOG_MODE',
        payload: mode,
    }
};