import React, {Component, Fragment} from 'react';
import {connect} from "react-redux";

class TableJobTypes extends Component {

    getRowsTable = () => {
        return this.props.arrJobType.map((item, i) => {
            return (
                <tr key={i}>
                    <th className="oid" scope="row">{item.oid}</th>
                    <td>{item.name}</td>
                    <td>{item.description}</td>
                    <td>{item.duration}</td>
                    <td>{item.costPerUnit}</td>
                    <td>{this.getServiceInterval(item.serviceInterval)}</td>
                    <td>{item.needPhoto ? "" : ""}</td>
                </tr>
            )
        })
    };

    getServiceInterval = (interval) => {
        if (interval === 0) return "";
        else if (interval === 1) return "E";
        else if (interval >= 2) return interval + " .";
    };

    render() {
        let rows = this.getRowsTable();
        return (
            <Fragment>
                {rows.length ?
                    <table className="table table-sm table-hover">
                        <thead className="thead-light">
                        <tr>
                            <th className="oidTHead" scope="col">oid</th>
                            <th scope="col"></th>
                            <th scope="col"></th>
                            <th scope="col"> ()</th>
                            <th scope="col">  ()</th>
                            <th scope="col"></th>
                            <th scope="col"> </th>
                        </tr>
                        </thead>
                        <tbody>
                        {rows}
                        </tbody>
                    </table>
                    :
                    <table className="table table-sm">
                        <thead className="thead-light">
                        <tr>
                            <th className="oidTHead" scope="col">oid</th>
                            <th className="width_10" scope="col"></th>
                            <th scope="col"></th>
                            <th scope="col"></th>
                            <th scope="col"> ()</th>
                            <th scope="col">  ()</th>
                            <th scope="col"></th>
                            <th scope="col"> </th>
                        </tr>
                        </thead>
                        <tbody>
                        <tr key="0">
                            <td colSpan="8" className="emptyText"> !</td>
                        </tr>
                        </tbody>
                    </table>
                }
            </Fragment>
        )
    }
}

//    store
const mapStateToProps = store => {
    return {
        arrJobType: store.administratorReducer.arrJobType,
    }
};
//   
const mapDispatchToProps = dispatch => {
    return {}
};
export default connect(
    mapStateToProps,
    mapDispatchToProps
)(TableJobTypes)